@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), local("Roboto-Regular"), url("https://fonts.gstatic.com/s/roboto/v15/2UX7WLTfW3W8TclTUvlFyQ.woff") format("woff"); }

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("https://fonts.gstatic.com/s/roboto/v15/d-6IYplOFocCacKzxwXSOD8E0i7KZn-EPnyo3HZu7kw.woff") format("woff"); }

html {
  background: #fafafa; }

html,
body {
  margin: 0;
  padding: 0;
  font-family: roboto, sans-serif;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

button:-moz-focus-inner {
  border: 0;
  padding: 0; }

button {
  cursor: pointer; }

.layout {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
  flex-flow: column;
  height: 100%;
  background-image: url("../imgs/bg-well.jpg"); }
  .layout h3,
  .layout p {
    color: #FFFFFF;
    font-size: 2em;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; }

.search {
  min-width: 400px; }

span {
  margin-right: 4px; }

.bg-layout {
  background-color: rgba(14, 14, 14, 0.5);
  padding: 40px 20px; }

.bg-latest {
  float: right; }

.col-md-5 {
  overflow: hidden;
  padding: 20px;
  border: 2px solid rgba(128, 128, 128, 0.493); }
  .col-md-5 > h4 {
    text-align: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; }

.col-md-7 {
  background-color: rgba(128, 128, 128, 0.267);
  padding-top: 20px;
  overflow: hidden; }
  .col-md-7:first-child {
    text-align: center; }

.row {
  padding: 10px; }

.thumbnail {
  overflow: hidden; }

html,
body {
  height: 100%; }

.scroller {
  overflow: hidden;
  overflow-y: scroll;
  width: 100%; }

.box {
  width: 50px;
  height: 50px;
  overflow: hidden; }

.footer {
  text-align: center; }

#footer-row {
  background-color: rgba(128, 128, 128, 0.267);
  color: #631192; }

a:hover {
  cursor: pointer;
  font-weight: bold; }
